    <p>
      Com o material escolhido e a temperatura definida no software fatiador,
      deve-se definir a posio do objeto sobre a mesa de impresso. Geralmente,
      quanto maior a superfcie em contato com a mesa, menor a chance do objeto
      descolar no meio da impresso (algo que acontece muito frequentemente).
    </p>
    
    <p>
      Alm disso, deve-se atentar se o objeto, quando pronto, sofrer estresse
      mecnico (por exemplo, um cabide que ter que suportar o peso da roupa),
      pois o objeto tem menos resistncia a tenses que foram a separao das
      camadas. A configurao de preenchimento (0 significa objeto oco e 100%
      objeto totalmente macio) pode ajudar a dar mais resistncia ao objeto,
      assim como a quantidade de paredes (permetros), ao custo de mais material
      e tempo para impresso.
    </p>
