<template>
  <div class="main">
    <h3>Qualidade vs. Velocidade</h3>
    <a href="#" v-scroll-to="'#introduction'" style="float: right"
      >Voltar ao topo</a
    >

    <p><i>Postado em 29 de janeiro de 2021</i></p>

    <img
      alt="Vue logo"
      title=" esquerda, camadas de menor altura e melhor resoluo;  direita, camadas mais altas e menor detalhamento"
      src="@/assets/3dprintweek/camadas01.jpg"
      width="40%"
      style="
        float: right;
        margin-left: 20px;
        margin-top: 0px;
        max-width: 500px;
        min-width: 200px;
      "
    />
    <p>
      Com a orientao do objeto em relao  mesa de impresso definida, a
      altura de cada camada que ser impressa  fator chave na qualidade dos
      detalhes do objeto. Quanto mais finas as camadas, mais detalhes podem ser
      capturados pelos movimentos da impressora, entretanto, mais camadas sero
      necessrias para imprimir o objeto, consequentemente, maior o tempo de
      impresso.
    </p>
    <p>
      Se qualidade no  um fator importante, por exemplo, para peas
      funcionais, pode-se usar camadas mais altas, para reduzir o tempo de
      impresso. A altura mxima que pode ser escolhida  no dimetro do bico de
      impresso (quanto maior o dimetro, maior altura de camada  possvel
      atingir). Um cuidado que se deve tomar,  o equilibrio entre temperatura,
      altura da camada e a velocidade dos movimentos.
    </p>
    <p>
      Embora exista o risco de se perder qualidade nos detalhes, pode-se
      aumentar a velocidade de movimento dos motores que controlam as direes
      horizontais e verticais (X e Y) do hotend. Com a necessidade de depositar
      (extrusar) plstico derretido de forma mais rpida, eventualmente a
      temperatura precisa ser calibrada (aumentar a temperatura para melhorar a
      fluidez do material). Esse equilbrio dos parmetros de velocidade,
      temperatura e altura de camada  o grande desafio do maker na hora da
      impresso algum objeto.
    </p>
    <p>
      As vezes, diferentes objetos, precisam de diferentes configuraes, mesmo
      utilizando o mesmo material e a prtica  a nica forma de atingir bons
      resultados. Quanto mais uma pessoa conhecer as caractersticas nicas da
      sua impressora, melhor sero os objetos impressos.
    </p>

